// ******************** code comments ********************
/*!   */
.btn-lv01{
    display:inline-block;
    text-align:center;
    @include rem(padding, 11px 15px 12px 15px);
    .txt{
        position:relative;
        vertical-align:middle;
        @include rem(font-size, 20px);
        line-height:1.4;
    }
    &.blue{
        border:1px solid skyblue;
        background-color:skyblue;
        color:#ffffff;
    }
    &.white{
        border:1px solid skyblue;
        background-color:#ffffff;
        color:red;
    }
    [class^="ico-"]{
        @include rem(margin-left, 10px);
    }
}

/*!   -  */
.btn-container{}

/*!   -  */
.btn-align-type01 {
    //   
    &.spacebetween-equal{
        display:flex;
        justify-content: space-between;
        @include rem(margin-right, -5px);
        [class^="btn-"]{
            display:flex;
            align-items:center;
            justify-content:center;
            @include rem(margin-right, 5px);
            flex: 0 1 100%;
            word-break: break-all;

            .txt{
                display:block;
                flex:0 1 auto;
            }
            [class^="ico-"]{
                flex:0 0 auto;
            }
        }
    }
    //  
    &.center{
        display:flex;
        justify-content: center;
        [class^="btn-"]{
            display:flex;
            align-items:center;
            justify-content:center;
            @include rem(margin-right, 5px);
            flex: 0 1 auto;
            word-break: break-all;

            &:last-child{
                margin-right:0;
            }
            .txt {
                display: block;
                flex: 0 1 auto;
            }
            [class^="ico-"] {
                flex: 0 0 auto;
            }
        }
    }
    //  
    &.left{
        display:flex;
        justify-content: flex-start;
        [class^="btn-"] {
            display: flex;
            align-items: center;
            justify-content: center;
            @include rem(margin-right, 5px);
            flex: 0 1 auto;
            word-break: break-all;

            &:last-child {
                margin-right: 0;
            }

            .txt {
                display: block;
                flex: 0 1 auto;
            }

            [class^="ico-"] {
                flex: 0 0 auto;
            }
        }
    }
    //  
    &.right {
        display: flex;
        justify-content: flex-end;
        [class^="btn-"] {
            display: flex;
            align-items: center;
            justify-content: center;
            @include rem(margin-left, 5px);
            flex: 0 1 auto;
            word-break: break-all;

            &:first-child {
                margin-left: 0;
            }
            .txt {
                display: block;
                flex: 0 1 auto;
            }
            [class^="ico-"] {
                flex: 0 0 auto;
            }
        }
    }
}
